.Galery-pieza{
    display: flex;
    flex-wrap: wrap;
    background-color: rgb(234, 241, 255);
    width: 912px;
    height:auto;
    overflow: hidden;
    
    border-radius: 5px;
    box-shadow: 2px 3px 3px 2px rgb(0, 0, 0);
   
    

}
.Galery-Multimedia{
    display: flex;
    justify-content: center;
    padding-top: 10px;
    background-color: rgb(234, 241, 255);
    width: auto;
    height: auto;
   

}

    

.Galery-info{
    /*margin-left: 650px;*/
    
    background-color: rgb(20, 23, 31);
    width: 304px;
    height: 580px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
   
   
    
    
    
    overflow: hidden;
    
    
}
.Galery-info p{
    margin-top: 70px;
    text-align: center;
    color: aliceblue;
    font-size: 25px;
    
    
}
.Galery-info h4{
    margin-left: 10px;
    margin-right: 5px;
    color: aliceblue;
    font-weight: lighter;
}
.Galery-info h2{
    background-color: rgb(0, 255, 98);
    margin-left: 50px;
    margin-right:50px ;
    margin-top: 40px;
    border-radius: 5px;
    padding-left: 25px;

}
.GalBtn{
    display: flex;
    flex-wrap: wrap;
    border-radius: 5px;
    position: absolute;
    
    background-color: rgb(3, 255, 234);
    width: 298px;
    height: 50px;
    margin-top: 20px;
    justify-content: center;
    padding-bottom: 3px;
    color: rgb(39, 40, 45);
    cursor: pointer;
    margin-left: 2px;
    
    
}
.GalBtn:hover {
    background-color:rgb(10, 241, 75) ;
    
    border: 1px solid rgb(10, 241, 75);
}
.Qa{
    margin-top: 3px;
    font-weight: bold;
}
.Qa1{
    font-size: 20px;
    margin-left: 8px;
    margin-top: 10px;
    font-weight: bold;
}
.GalBtn p{
    font-weight: bold;
    margin-top: 0px;
    font-size: 20px;
    color: rgb(39, 40, 45);

}


.GalBtn i{
   
    margin-left: 5px;
    font-size: 30px;
    
}
.DescripcionSS{
    width:auto;
    height: 180px;

}

@media  screen and (max-width:1229px) {
    .Galery-Multimedia{
        background-color: blueviolet;
        overflow: hidden;
    }
    .Galery-pieza{
        background-color: blue;
        display: flex;
        justify-content: center;
        width: 100%;
    }
    .Galery-info{
        width: 100%;
    }
    
    
}